.friend-list-popup, .profile-edit-popup {
    position: fixed;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
    padding: 20px;
    background-color: white;
    border-radius: 28px;
    /* box-shadow: 0 0 10px #AD45FF; */
    z-index: 9998;
    color: black;
    align-items: center;
    text-align: center;
}

.friend-subtext {
    font-size: 20px;
    color: #656565;

}

.close-button {
    position: absolute;
    padding: 35px;
    top: 0;
    right: 0;
    background: none;
    border: none;
    font-size: 1.5em;
    font-weight: bold;
    cursor: pointer;
}

input .friend-search {
    background-color: gray;
    color: black;
    height: 25px;
    padding: 5px;
    border: none;
  }

input.friend-search::placeholder {
    color: black;
}

.friend-search {
    background-color: #E5E5E5;
    border-radius: 20px;
    border: none;
    padding: 20px;
    color: black;
    width: 100%;
    height: 20px;
    font-size: 1.0rem;
}

.friend-container {
    text-align: left;
    display: flex;
}

.friend-info {
    margin-left: 0.5em;
}

.friend-name {
    font-weight: bold;
    /* margin-bottom: 5px;   */
    font-size: .8em;
    /* margin-right:20px; */
}

.friend-username {
    font-size: .5em;
    font-weight: lighter;
    margin-top: 0.2em;
}

.friend-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;

    margin-bottom: 20px; 
    padding: 20px;
    background-color: #0a0a0a; 
    border-radius: 20px; 
    box-shadow: 0 0 10px rgba(35, 30, 30, 0.2);
    width: 100%; 
}

.friend-action {
    display: flex;
}

.friend-action button {
    margin-right: 10px;
   
    padding: 10px 20px; 
    border: none;
    border-radius: 20px; 
    cursor: pointer;
    font-weight: bold; 
    color: #656565; 
    background-color: #1A1A1A; 
    transition: border 0.1s;
}

.popup-subtitle {
    font-size: 0.8em;
    margin-bottom: 25px;
    font-weight: 200;
    color: #656565;
}

.post-input, .profile-input {
    background-color: #000;
    border-radius: 10px;
    color: #fff;
    width: 80%;
    height: 13vh;
    padding: 15px;
}

.post-input::placeholder {
    color: #fff;
}

.post-upload {
    background-color: #000;
    color: #fff;
    border-radius: 20px;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    width: 100%;
    height: 5vh;
    /* margin-top: 10px; */
}

.post-upload{
    height: 4.5vh;
    box-sizing: border-box;
    outline: none;
}

.post-upload::placeholder {
    color: #fff;
}

.image-input::placeholder {
    color: #fff;
}

.upload-button {
    /* position: absolute; */
    /* right: 1em; */
    /* top: 50%; */
    /* transform: translateY(-50%); */
    background-color: black;
    color: white;
    border-radius: 1em;
    border: none;
    padding: 0.5em 1em;
    cursor: pointer;
    /* margin-left: -30%; */
    width: 80%;
}

.upload-button:hover {
    background-color: gray;
    color: white;
}


.post-input,
.post-upload {
    resize: none;
}

.button-group {
    display: flex;
    justify-content: center;
    gap: 10px;
}

.button-group .cancel-button {
    background-color: #656565;
    color: #ffffff;
    border-radius: 20px;
    height: 50px;
    border: none;
    cursor: pointer;
    width: 150px;
    padding: 0px 20px;
    font-size: 23px;
    font-weight: bold;
}

.button-group .cancel-button:hover {
    background-color: #656565;
}

.button-group .post-button {
    background-color: #AD45FF;
    color: #ffffff;
    border-radius: 20px;
    height: 50px;
    border: none;
    cursor: pointer;
    width: 150px;
    padding: 0px 20px;
    font-size: 23px;
    font-weight: bold;
}

.notification-list {
    max-height: 480px; 
    overflow-y: auto;  
  }

  .unfriend {
    background-color: red;
    color: white;
    border: none;
    padding: 8px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s; 
    cursor: pointer;
    border-radius: 4px;
  }
  
  .unfriend:hover {
    background-color: white;
    color: red;
    border: 1px solid red;
  }

